import { CreditCard, Users, ShoppingCart, PackageCheck } from "lucide-react";
import { motion } from "framer-motion";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  Tooltip,
  ResponsiveContainer,
} from "recharts";
import { Helmet } from "react-helmet";
import { useEffect, useState } from "react";

const chartData = [
  { name: "فروردین", sales: 20000000 }, // Farvardin
  { name: "اردیبهشت", sales: 70000000 }, // Ordibehesht
  { name: "خرداد", sales: 32000000 }, // Khordad
  { name: "تیر", sales: 421000000 }, // Tir
  { name: "مرداد", sales: 114000000 }, // Mordad
  { name: "شهریور", sales: 12000000 }, // Shahrivar
  { name: "مهر", sales: 94500000 }, // Mehr
  { name: "آبان", sales: 421000000 }, // Aban
  { name: "آذر", sales: 94500000 }, // Azar
  { name: "دی", sales: 114000000 }, // Dey
  { name: "بهمن", sales: 20000000 }, // Bahman
  { name: "اسفند", sales: 32000000 }, // Esfand
];

const Overview = () => {
  const [activeUser, setActiveUsers] = useState(0);
  const [productLength, setProductLength] = useState(0);

  const ACTIVE_USER_API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/user/"
      : "https://allmaxsport.com/api/user/";

  const ACTIVE_PRODUCTS_API_URL =
    window.location.hostname === "localhost"
      ? "http://127.0.0.1:8000/api/products/"
      : "https://allmaxsport.com/api/products/";

  useEffect(() => {
    async function loadActiveUsers() {
      const response = await fetch(
        `${ACTIVE_USER_API_URL}?get_active_users_count=true`
      );
      const data = await response.json();
      const activeUserCount = data.active_users_count;
      setActiveUsers(activeUserCount);
    }
    loadActiveUsers();

    async function loadProductLength() {
      const response = await fetch(ACTIVE_PRODUCTS_API_URL);
      const data = await response.json();
      setProductLength(data.length);
    }

    loadProductLength();
  }, []);

  const stats = [
    {
      title: "سفارشات امروز",
      value: "۲۵",
      icon: <ShoppingCart size={20} />,
      color: "bg-blue-100 text-blue-600",
    },
    {
      title: "کاربران جدید",
      value: Number(activeUser).toLocaleString("fa-IR"),
      icon: <Users size={20} />,
      color: "bg-green-100 text-green-600",
    },
    {
      title: "درآمد امروز",
      value: "۳٬۲۰۰٬۰۰۰ تومان",
      icon: <CreditCard size={20} />,
      color: "bg-yellow-100 text-yellow-600",
    },
    {
      title: "محصولات موجود",
      value: Number(productLength).toLocaleString("fa-IR"),
      icon: <PackageCheck size={20} />,
      color: "bg-purple-100 text-purple-600",
    },
  ];

  return (
    <>
      <Helmet>
        <title>المکس | داشبورد</title>
      </Helmet>
      <div className="space-y-6">
        {/* Title */}
        <h2 className="text-2xl font-bold text-gray-800 dark:text-white">
          داشبورد
        </h2>

        {/* Stats Cards */}
        <div className="grid md:grid-cols-4 gap-6">
          {stats.map((item, idx) => (
            <motion.div
              key={idx}
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              transition={{ delay: idx * 0.1 }}
              className="bg-white dark:bg-neutral-800 p-4 rounded-xl shadow-sm border border-gray-100 dark:border-neutral-700 flex items-center gap-4"
            >
              <div
                className={`w-10 h-10 rounded-full flex items-center justify-center ${item.color}`}
              >
                {item.icon}
              </div>
              <div className="text-right">
                <p className="text-sm text-gray-500 dark:text-gray-400">
                  {item.title}
                </p>
                <p className="text-lg font-bold text-gray-800 dark:text-white">
                  {item.value}
                </p>
              </div>
            </motion.div>
          ))}
        </div>

        {/* Sales Chart */}
        <div className="bg-white dark:bg-neutral-800 p-4 sm:p-6 rounded-2xl shadow-lg border border-gray-200 dark:border-neutral-700 transition hover:shadow-xl">
          <h3 className="text-lg sm:text-xl font-bold text-gray-800 dark:text-white mb-2">
            📈 نمودار فروش ماهانه
          </h3>
          <p className="text-xs sm:text-sm text-gray-500 dark:text-gray-400 mb-4 border-b border-gray-100 dark:border-neutral-700 pb-3">
            عملکرد فروش در ۱۲ ماه گذشته (تومان)
          </p>

          <div className="overflow-x-auto">
            <ResponsiveContainer width="100%" height={300}>
              <LineChart
                data={chartData}
                margin={{ top: 10, right: 20, left: -10, bottom: 0 }}
              >
                <defs>
                  <linearGradient id="colorSales" x1="0" y1="0" x2="0" y2="1">
                    <stop offset="0%" stopColor="#3b82f6" stopOpacity={0.8} />
                    <stop offset="100%" stopColor="#3b82f6" stopOpacity={0.1} />
                  </linearGradient>
                </defs>
                <XAxis
                  dataKey="name"
                  stroke="#cbd5e1"
                  tick={{ fontSize: 11, fill: "#94a3b8" }}
                  axisLine={{ stroke: "#e2e8f0" }}
                  interval={0} // Always show all labels
                  minTickGap={-10}
                />
                <YAxis
                  stroke="#cbd5e1"
                  tick={{ fontSize: 11, fill: "#94a3b8" }}
                  axisLine={{ stroke: "#e2e8f0" }}
                  tickFormatter={(value) =>
                    `${(value / 1_000_000).toFixed(0)}M`
                  }
                />
                <Tooltip
                  formatter={(value) =>
                    `${Number(value).toLocaleString("fa-IR")} تومان`
                  }
                  contentStyle={{
                    backgroundColor: "#1e293b",
                    borderRadius: "8px",
                    border: "none",
                    color: "#fff",
                    fontSize: "0.75rem",
                    boxShadow: "0 2px 8px rgba(0,0,0,0.15)",
                    textAlign: "right",
                  }}
                  labelStyle={{
                    color: "#cbd5e1",
                    fontWeight: "bold",
                    textAlign: "right",
                  }}
                  itemStyle={{ color: "#fff", textAlign: "right" }}
                />
                <Line
                  type="monotone"
                  dataKey="sales"
                  stroke="url(#colorSales)"
                  strokeWidth={3}
                  dot={{
                    r: 4,
                    stroke: "#3b82f6",
                    strokeWidth: 2,
                    fill: "#fff",
                  }}
                  activeDot={{
                    r: 6,
                    fill: "#3b82f6",
                    stroke: "#2563eb",
                    strokeWidth: 3,
                  }}
                />
              </LineChart>
            </ResponsiveContainer>
          </div>
        </div>

        {/* Latest Orders */}
        <div className="bg-white dark:bg-neutral-800 p-6 rounded-xl shadow-sm border border-gray-100 dark:border-neutral-700">
          <h3 className="text-lg font-bold text-gray-800 dark:text-white mb-4">
            آخرین سفارشات
          </h3>
          <div className="space-y-4">
            {[1, 2, 3].map((id) => (
              <div
                key={id}
                className="flex justify-between items-center text-sm border-b pb-2 text-gray-600 dark:text-gray-300"
              >
                <span>سفارش #{1000 + id}</span>
                <span>۲,۰۰۰,۰۰۰ تومان</span>
                <span>در حال پردازش</span>
              </div>
            ))}
          </div>
        </div>
      </div>
    </>
  );
};

export default Overview;
